@import '../abstracts/variables';
@import '../abstracts/mixins';

.final-cta {
  padding: $space-xl 0;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);

  color: white;
  text-align: center;

  .container {
    max-width: $container-width;
    margin: 0 auto;
    padding: 0 $space-md;
  }

  .cta-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: $space-lg;
    align-items: center;

    @media (width <= 768px) {
      grid-template-columns: 1fr;
      gap: $space-md;
    }
  }

  .cta-content {
    text-align: left;

    @media (width <= 768px) {
      // text-align: center;
    }
    .contact-content {
      text-align: left;

      h2 {
        font-size: $font-size-xl;
        margin-bottom: $space-sm;
        color: $text-primary;
        position: relative;
        padding-bottom: $space-sm;

        &::after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          width: 50px;
          height: 3px;
          background: $brand-color;
        }
      }

      .contact-text {
        font-size: $font-size-md;
        color: $text-secondary;
        line-height: 1.8;
        margin-bottom: $space-lg;
      }

      h3 {
        font-size: 1.5rem;
        margin-bottom: $space-md;
        color: $brand-color;
        font-style: italic;
        font-weight: $font-weight-regular;
      }

      .contact-expectations {
        list-style: none;
        padding: 0;
        margin-bottom: $space-lg;

        li {
          font-size: $font-size-md;
          color: $text-secondary;
          line-height: 1.8;
          margin-bottom: $space-sm;
          position: relative;
          padding-left: $space-md;

          &:before {
            content: '✓';
            position: absolute;
            left: 0;
            color: $brand-color;
            font-size: 1.2rem;
          }
        }
      }
    }

    .contact-info {
      margin-top: $space-md;

      .contact-item {
        display: inline-block;
        margin: 0 $space-md;
        font-size: 1.1rem;

        i {
          margin-right: $space-xs;
        }

        a {
          color: white;
          text-decoration: none;
          border-bottom: 1px solid rgb(255 255 255 / 30%);
          transition: border-color 0.3s ease;

          &:hover {
            border-color: white;
          }
        }
      }

      @media (width <= 768px) {
        .contact-item {
          display: block;
          margin: $space-xs 0;
        }
      }
    }
  }

  .cta-form {
    background-color: $white;
    padding: $space-md;
    border-radius: 8px;
    box-shadow: 0 4px 12px rgb(0 0 0 / 10%);
    color: $text-primary;

    h3 {
      font-size: 1.5rem;
      margin-bottom: $space-sm;
      color: $text-primary;
    }

    .wpcf7-form {
      p {
        margin-bottom: 0;
      }

      .form-row {
        display: flex;
        flex-wrap: wrap;
        gap: $space-sm;
        margin-bottom: $space-sm;

        &.form-row-submit {
          justify-content: flex-start;
          margin-top: $space-md;
        }
      }

      .form-col {
        flex: 1;
        min-width: 0;

        &.form-col-full {
          flex: 0 0 100%;
        }
      }

      label {
        display: block;
        font-size: 0.9rem;
        margin-bottom: $space-xs;
        color: $text-primary;
        font-weight: 500;
        text-align: left;
      }

      input[type='text'],
      input[type='email'],
      input[type='tel'],
      textarea {
        width: 100%;
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 0;
        font-size: 1rem;
        background: $white;
        color: $text-primary;
        box-sizing: border-box;

        &:focus {
          outline: none;
          border-color: $brand-color;
          box-shadow: none;
        }

        &::placeholder {
          color: #999;
          font-size: 0.9rem;
        }
      }

      textarea {
        min-height: 100px;
        resize: vertical;
      }

      input[type='submit'] {
        display: inline-block;
        background-color: $brand-color;
        color: white;
        font-weight: $font-weight-bold;
        font-size: 1rem;
        padding: 10px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &:hover {
          background-color: darken($brand-color, 10%);
        }
      }

      .wpcf7-response-output {
        margin-top: $space-sm;
        font-size: 0.9rem;
        color: $text-primary;
      }
    }

    @media (width <= 768px) {
      .form-row {
        flex-direction: column;
        gap: $space-xs;
      }

      .form-col {
        flex: 0 0 100%;
      }
    }
  }

  @media (width <= 768px) {
    h2 {
      font-size: 1.75rem;
    }

    .supporting-text {
      font-size: 1rem;
    }
  }
}
